
import * as React from 'react';
import Box from '@mui/material/Box';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
import { AppBar, Button, Container, createMuiTheme, Grid, IconButton, Toolbar } from '@mui/material';
import BalanceIcon from '@mui/icons-material/Balance';
import MenuIcon from '@mui/icons-material/Menu';
import { Select, Option } from '@mui/joy';
import StorageHelper from '../utils/StorageHelper';
import { Language, StorageKeys } from '../utils/Defaults';
import { LanguageModel } from '../models/GeneralModels';
import { NavigationTexts } from '../utils/MainTexts';


export interface INavigationProps {
 languageChange:any;
}

interface INavigationState {
    serviceMEnuOpen: boolean;
    anchorEl: HTMLElement | undefined;
    width: number;
    changed: boolean;
}


export class Navigation extends React.Component<INavigationProps, INavigationState> {
    public static containerId: string = 'navigationId';
    private fontFamily: string = 'Garamond';
    private currentLanguage: LanguageModel = { currentLanguage: Language.BG, changed: false };

    constructor(props: INavigationProps) {
        super(props);

        this.state = {
            serviceMEnuOpen: false,
            anchorEl: undefined,
            width: window.innerWidth,
            changed: false
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }

    public render() {
        this.currentLanguage = StorageHelper.getCurrentLanguage();
        let ctrl = <></>;
        ctrl = (
            <React.Fragment>
                <AppBar position='sticky' id="header" sx={{ top: '0', backgroundColor: '#3a4862', position: '-webkit-sticky' }}>
                    {
                        this.state.width < 900
                            ?
                            <Toolbar>
                                <Grid container spacing={{ xs: 1, sm: 2, md: 4 }} >
                                    <Grid item sm={1} xs={1} >
                                        <IconButton
                                            size="large"
                                            edge="start"
                                            color="inherit"
                                            aria-label="menu"
                                            sx={{ mr: 2 }}
                                            onClick={(e) => { this.setState({ serviceMEnuOpen: true, anchorEl: e.currentTarget }) }}
                                        >
                                            <MenuIcon />
                                        </IconButton>
                                        <Menu
                                            id="menu-appbar"
                                            anchorEl={this.state.anchorEl}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            keepMounted
                                            transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'right',
                                            }}
                                            open={this.state.serviceMEnuOpen}
                                            onClose={() => { this.setState({ serviceMEnuOpen: false }) }}
                                        >
                                            <MenuItem onClick={() => {
                                                const element = document.getElementById('services');
                                                const header = document.getElementById('header');

                                                if (element && header) {
                                                    window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                                }
                                            }}>{NavigationTexts[this.currentLanguage.currentLanguage].services}</MenuItem>
                                            <MenuItem onClick={() => {
                                                const element = document.getElementById('team');
                                                const header = document.getElementById('header');

                                                if (element && header) {
                                                    window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                                }
                                            }}>{NavigationTexts[this.currentLanguage.currentLanguage].team}</MenuItem>
                                            <MenuItem onClick={() => {
                                                const element = document.getElementById('contacts');
                                                const header = document.getElementById('header');

                                                if (element && header) {
                                                    window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                                }
                                            }}>{NavigationTexts[this.currentLanguage.currentLanguage].contacts}</MenuItem>
                                        </Menu>
                                    </Grid>
                                    <Grid item sm={8} xs={8} style={{ textAlign: 'center', fontFamily: this.fontFamily, fontSize: '20px', marginTop: '7px', marginLeft: '15px' }}>
                                        {NavigationTexts[this.currentLanguage.currentLanguage].lawFirm.place+' '+NavigationTexts[this.currentLanguage.currentLanguage].lawFirm.name}
                                        </Grid>
                                    {this.currentLanguage.currentLanguage === Language.BG
                                        ?
                                        <></>
                                        : <Grid item sm={1} xs={1} style={{ textAlign: 'center', fontFamily: this.fontFamily }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '15px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.BG, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>BG</Button>
                                        </Grid>}
                                    {this.currentLanguage.currentLanguage === Language.EN
                                        ? <></> :
                                        <Grid item sm={1} xs={1} style={{ textAlign: 'center', fontFamily: this.fontFamily, paddingRight: '0px' }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '15px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.EN, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>EN</Button>
                                        </Grid>}
                                    {this.currentLanguage.currentLanguage === Language.FR
                                        ? <></> :
                                        <Grid item sm={1} xs={1} style={{ textAlign: 'center', fontFamily: this.fontFamily }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '15px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.FR, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>FR</Button>
                                        </Grid>}
                                </Grid>
                            </Toolbar>
                            :
                            <Toolbar>
                                <Grid container spacing={{ xs: 1, sm: 1, md: 1 }} >
                                    <Grid item sm={3} md={3} style={{ textAlign: 'center', fontFamily: this.fontFamily, fontSize: '20px' }}>
                                        {/* <BalanceIcon /> */}
                                        <Typography sx={{ minWidth: 130, color: '#FFFFFF', fontFamily: this.fontFamily, fontSize: '20px' }}
                                            onClick={() => {
                                                window.scrollTo(0, 0)
                                            }}
                                        >
                                            {NavigationTexts[this.currentLanguage.currentLanguage].lawFirm.place}
                                            <br />
                                            {NavigationTexts[this.currentLanguage.currentLanguage].lawFirm.name}
                                        </Typography>
                                    </Grid>
                                    <Grid item sm={2} md={2} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px' }}
                                    >
                                        <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                            const element = document.getElementById('services');
                                            const header = document.getElementById('header');

                                            if (element && header) {
                                                window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                            }

                                        }}>{NavigationTexts[this.currentLanguage.currentLanguage].services}</Button>

                                    </Grid>
                                    <Grid item sm={2} md={2} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px' }}>
                                        <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                            const element = document.getElementById('team');
                                            const header = document.getElementById('header');

                                            if (element && header) {
                                                window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                            }
                                        }}>{NavigationTexts[this.currentLanguage.currentLanguage].team}</Button>
                                    </Grid>
                                    <Grid item sm={2} md={2} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px' }}>
                                        <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                            const element = document.getElementById('contacts');
                                            const header = document.getElementById('header');

                                            if (element && header) {
                                                window.scrollTo(0, element.offsetTop - header.offsetHeight);
                                            }
                                        }}>{NavigationTexts[this.currentLanguage.currentLanguage].contacts}</Button>
                                    </Grid>

                                    {this.currentLanguage.currentLanguage === Language.BG
                                        ?
                                        <></>
                                        : <Grid item sm={1} md={1} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px' }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.BG, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>BG</Button>
                                        </Grid>}
                                    {this.currentLanguage.currentLanguage === Language.EN
                                        ? <></> :
                                        <Grid item sm={1} md={1} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px', paddingRight: '0px' }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.EN, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>EN</Button>
                                        </Grid>}
                                    {this.currentLanguage.currentLanguage === Language.FR
                                        ? <></> :
                                        <Grid item sm={1} md={1} style={{ textAlign: 'center', fontFamily: this.fontFamily, marginTop: '10px' }}>
                                            <Button style={{ fontFamily: this.fontFamily, fontSize: '20px' }} color="inherit" onClick={() => {
                                                let model: LanguageModel = { currentLanguage: Language.FR, changed: true }
                                                StorageHelper.set(StorageKeys.keyLanguage, model);
                                                this.setState({ changed: true })
                                                this.props.languageChange();
                                            }}>FR</Button>
                                        </Grid>}
                                </Grid>
                            </Toolbar>
                    }
                </AppBar>
            </React.Fragment>
        );

        return ctrl;
    };

    updateDimensions = () => {
        this.setState({ width: window.innerWidth });
    };
}

export default Navigation;
