export interface IAppState {
  changed: boolean;
};

initializeIcons();
export class App extends Component<any, IAppState> {
  constructor(props: any) {
    super(props);
    this.state = {
      changed: false
    };
  }
  
  public render() {
    let ctrl = <></>;
    ctrl = (
      <div className="App">
        <Navigation languageChange={()=>{this.setState({changed:true})}} />
        <WelcomeScreen />
        <Footer />
      </div>
    )
    return ctrl;
  }
}

export default App;
