        martinResume: 'M. Martin Georgiev est admis au Barreau de Sofia en tant qu\'avocat avec le titre "Advocate" (Адвокат) en République de Bulgarie et il est autorisé à exercer en tant que tel sur le territoire du pays, dédiée au droit des sociétés civiles et commerciales, et au droit des affaires. Il a développé une importante expérience et expertise dans le domaine de droit des sociétés civiles et commerciales, et de droit des affaires. Il se distingue par ses connaissances approfondies dans le domaine des transactions internationales conformément à la législation bulgare ou anglaise. Il possède un savoir-faire de longue date dans la préparation et la gestion de transactions internationales d\'entreprises, y compris la rédaction de documents et la négociation avec des PME ; la création d\'entités ; conditions de négociation ; opérations de fusions-acquisitions et assistance dans les procédures de marchés publics. Des services sont également fournis pour l\'initiation de nouveaux dossiers, ainsi que la préparation d\'avis juridiques sur des dossiers déjà déposés ou en attente. Assistance dans la préparation de tous types d’accords et de plaidoyers en bulgare et en anglais, y compris la correspondance officielle dans les deux langues. Tout au long de sa carrière, M. Georgiev a conseillé une grande variété d\'institutions internationales, de banques, d\'organismes gouvernementaux et de PME et a aidé à la conclusion de nombreux accords, y compris des accords de confidentialité, des prêts syndiqués, des accords de prêt garantis et non garantis, des mémorandums et beaucoup d’autre.',
