import { LanguageModel } from "../models/GeneralModels";
import { Language, StorageKeys } from "./Defaults";





export class StorageHelper {



    public static clear() {
        sessionStorage.clear();
    };

    public static set(key: string, model: any) {
        var d = JSON.stringify(model);
        sessionStorage.setItem(key, JSON.stringify(model));
    }

    public static getCurrentLanguage(): LanguageModel {
        let data = sessionStorage.getItem(StorageKeys.keyLanguage);
        let item: LanguageModel = {currentLanguage: Language.BG,changed:false}; 
        if (data != null) {
            item = JSON.parse(data);
        }

        return item;
    };
};

  
export default StorageHelper;